// 教师发展
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import data from '../../../data/teacher/教师视角--教师发展.json'
export default class SelectionSituation extends Component {
  constructor(props){
    super(props);
    this.state = {
      country0:data.RECORDS.teachingMasterNum,
      country1:data.RECORDS.virtuePacesetterNum,
      country2:data.RECORDS.prominentTeacherNum,
      country3:data.RECORDS.greatMasterWork,
      workHome0:data.RECORDS.teachingMasterWork,
      workHome1:data.RECORDS.scientificPayoffsNum,
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>省级以上教学名师：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.country0}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>省级以上师德标兵：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.country1}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>省级以上优秀教师：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.country2}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>大师工作室：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.workHome0}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>名师工作室：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.workHome1}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_development_label'}>省级以上教科研成果：</label>
          <span  className={'teacher_visual_teacher_development_span'}>{this.state.country3}</span>
        </Col>
      </Row>
    )
  }
}
