// 教师引入
import React, { Component } from 'react';
import { Col } from 'antd';
import OtherChart from '../../../common/components/BaseEcharts/OtherChart';
import jsonData from '../../../data/teacher/教师视角--教师引入.json';

export default class TeacherIntroduce extends Component {
  

  render() {
    let option = {
      title: {
        show: false,
      },
      grid: {
        top: '50%',
        // left: '3%',
        // right: '3%',
        // bottom: '25%',
        containLabel: false,
      },
      tooltip: {},
      legend: {
        show: false,
      },
      radar: {
        shape: 'circle',
        center: ['50%', '55%'],
        nameGap :0,
        axisLine: {
          lineStyle: {
            color: '#00CFFF',
          },
        },
        name: {
          textStyle: {
            color: '#00CFFF',
            borderRadius: 3,
            padding: [0, 0],
          },
        },
        indicator: [
          { name: '系统评价', max: 100 },
          { name: '学生评价', max: 100 },
          { name: '同行评价', max: 100 },
          { name: '教学督导', max: 100 }
        ],
      },
      series: [{
        name: '',
        type: 'radar',
        areaStyle: {
          normal: {
            color: '#00CFFF',
          },
        },
        lineStyle: {
          color: '#00CFFF',
        },
        symbolSize:0,
        data: [
          {
            value: jsonData.RECORDS,
            name: '',
          },
        ],
      }],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <OtherChart
          id='TeacherIntroduce'
          data={option}
          height='100%'/>
      </Col>
    );
  }
}
