// 教师数量
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import data from '../../../data/teacher/教师视角--教师发展--教师数量.json'
export default class SelectionSituation extends Component {
  constructor(props){
    super(props);
    this.state = {
      lead:data.RECORDS[0].professionalLeadersNum,
      backbone:data.RECORDS[0].coreTeacher,
      courtyard:data.RECORDS[0].teachingMasterNum,
      ten:data.RECORDS[0].topTeacherNum
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={24}>
          <label className={'teacher_visual_teacher_num_label'}>专业带头人数：</label>
          <span  className={'teacher_visual_teacher_num_span'}>{this.state.lead}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_num_label'}>骨干教师数：</label>
          <span  className={'teacher_visual_teacher_num_span'}>{this.state.backbone}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_num_label'}>院级教学名师数：</label>
          <span  className={'teacher_visual_teacher_num_span'}>{this.state.courtyard}</span>
        </Col>
        <Col span={24}>
          <label className={'teacher_visual_teacher_num_label'}>院级十佳教师数：</label>
          <span  className={'teacher_visual_teacher_num_span'}>{this.state.ten}</span>
        </Col>
      </Row>
    )
  }
}
