// 社会兼职
import React, {Component} from 'react';
import {Col} from 'antd';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import jsonData from '../../../data/teacher/教师视角--社会兼职.json';

export default class PartTimeJob extends Component {
  
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: jsonData.RECORDS.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      color:['#4A90E2'],
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show:false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          data: jsonData.RECORDS.data,
          label: {
            normal: {
              color:'#fff',
              show: true,
              position: 'top'
            }
          },
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='PartTimeJobBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
